h1,h2,h3,h4,p{
    padding: 0;
    margin: 0;
}

h1{
    font-family: 'Fivo Sans Bold';
    font-size: 50px;
    color:$black;
}

h2{
    font-family: 'Fivo Sans Bold';
    font-size: 50px;
    color:$black;
}

h3{
    font-family: 'Fivo Sans Bold';
    font-size: 24px;
    color:$black;
}


p{
    font-family: 'Fivo Sans Light';
    font-size: 18px;
    color: $gray;
}

.headbg{
    background: url("../img/headbg.jpg") no-repeat center;
    background-size: cover;
}


.container{
    max-width: 1410px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}

.page-header{
    padding-top: 85px;
    padding-bottom: 450px;
    
        @media (max-width:800px) {
            padding-bottom: 50px;
         }

    &__menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
      @media (max-width:1000px) {
           
            flex-direction: column;
        }
        &__logo{
            font-family: 'Fivo Sans Heavy';
            font-size: 32px;
            text-decoration: none;
            color: $acient;
            flex-basis: 29%;
            @media (max-width:1000px) {
            margin-bottom: 50px;
            }
        }
        &__links{
            display: flex;
            padding: 0;
            
            @media (max-width:1000px) {
                transform: translate(-25px, 36px);
                flex-direction: column;
                transform: scale(0);
                display: none;
                
            }
            li{
                margin-left: 67px;
                list-style-type: none;
                @media (max-width:1000px) {
                  padding-top: 20px;
                }
                a{
                    text-decoration: none;
                    color: $gray;
                    font-family: 'Fivo Sans Regular';
                    font-size: 18px;
                    @media (max-width:1000px) {
                        padding-top: 20px;
                        font-size: 22px;
                      }
                }
            }
        }
        &__login{
            text-decoration: none;
            color: #fff;
            background: $acient;
            padding: 14px 46px;
            border-radius: 21px;
            font-family: 'Fivo Sans Regular';
            @media (max-width:1000px) {
               margin-top: 30px;
            }
        }
        &__hamburger{
            height: 21px;
            width: 30px;
            position: relative;
            background: transparent;
            display: none;
            @media (max-width:1000px) {
                display: block;
            }
        span, span::after, span::before{
            width: 30px;
            height: 2px;
            content: "";
            background: $acient;
            position: absolute;
            top:50%;
            display: block;
        }
        span::after{
            transform: translatey(-8px);
            transition: 0.2s ease-in-out;
       
        }
        span::before{
            transform: translatey(6px);
            transition: 0.2s ease-in-out;
            
            }
    }
}
    &__text{
     max-width: 542px;
        padding-top: 180px;
        padding-left: 120px;
     @media (max-width:800px) {
         padding-left: 0;
     }
     h1{
                
        @media (max-width:600px) {
            font-size:9vw;  
        }   
}
        &__first{
            padding-bottom: 26px;
            padding-top: 67px;
            letter-spacing: 0.3px;
            line-height: 2.1;
            @media (max-width:600px) {
                font-size:6vw;  
            }   
        }
        &__second{
         
            line-height: 2.2;
            max-width: 465px;
            @media (max-width:600px) {
                font-size:6vw;  
            }   
        }
        &__buttons{
            padding-top: 73px;
            display: flex;
            @media (max-width:600px) {
               flex-direction: column;
               text-align: center;
            }
           
            &__service{
                text-decoration: none;
                color: #fff;
                background: $acient;
                padding: 28px 87px;
                border-radius: 40px;
                font-family: 'Fivo Sans Regular';
                box-shadow: -6px 6px 12px 0px #f09699;
                transition: 0.2s ease-in-out;
            }
            &__service:hover{
                box-shadow: none;
                border: 2px solid $acient;
                color: $acient;
                transition: 0.2s ease-in-out;
                background: transparent;
                
            }
            &__signup{
                background: transparent;
                text-decoration: none;
                border:2px solid $acient;
                color: $acient;
                padding: 28px 83px;
                border-radius:40px;
                font-family: 'Fivo Sans Regular';
                margin-left: 24px;
                transition: 0.2s ease-in-out;
                @media (max-width:600px) {
                    margin-top: 30px;
                    margin-left: 0;
                 }
            }
            &__signup:hover{
                background: $acient;
                transition: 0.2s ease-in-out;
                color: #fff;
                box-shadow: 7px 6px 12px 0px #f09699;
            }
        }
    }
}

.page-features{
  
    padding-bottom: 120px;
    &__content{
        text-align: center;
        padding-bottom: 136px;
        h2{
            @media (max-width:600px) {
                font-size:8vw;  
            }   
        }
        p{
            padding-top: 50px;
            max-width: 660px;
            margin: 0 auto;
        }
    }
    &__block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        @media (max-width:800px) {
            flex-direction: column;
           justify-content: center;
         }
        &__item{
            flex-basis: 21%;
            h3{
                padding-top: 95px;
                padding-bottom: 35px;
            }
        }
    }
}

.page-whats{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 325px;
    
        @media (max-width:800px) {
          padding-bottom: 50px;
         }

    @media (max-width:1000px) {
        flex-direction: column;
        justify-content: center;
     }
    &__item{
   img{
       width: 90vmin;
       height: 90vmin;
   }
        &__text{
            position: relative;
          
            h3{
                   padding-top: 60px;
                padding-bottom: 27px;
            }
            p{
                max-width: 465px;
            }
            &__icon{
                position: absolute;
                color: $acient;
                left: -51px;
                font-size: 32px;
                top: 53px;
              
            }
            &__iconfirst{
                position: absolute;
                color: $acient;
                left: -51px;
                font-size: 32px;
                top: 146px;
            }
            &__first{
                padding-top: 90px;
            }
        }
    }
}
.page-power{
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width:1000px) {
        flex-direction: column-reverse;
        justify-content: center;
     }
    &__item{
        flex-basis: 50%;
        img{
            width: 90vmin;
            height: 90vmin;
        }
        &__up{
            font-family: 'Fivo Sans Heavy Oblique';
            font-size: 20px;
            color: $gray;
            
                @media (max-width:800px) {
                
                    font-size:5vw;
                   
                 }
            
        }
        p{
            padding-left: 50px;
            padding-bottom: 80px;
            padding-top: 70px;
            max-width: 374px;
            
                @media (max-width:800px) {
                    padding-left: 0;
                    margin: 0 auto;
                    font-size:7vw;
                    padding-top: 30px;
                 }
        
        }
        a{
            margin-left: 50px;
           
            text-decoration: none;
            color: #3cbef1;
            font-family: 'Fivo Sans Heavy Oblique';
            font-size: 18px;
        }
        h2{
            @media (max-width:800px) {
                padding-left: 0;
                margin: 0 auto;
                font-size:7vw;
                
             }
        }
    }
}
.page-ways{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 300px;
    padding-bottom: 175px;
    @media (max-width:1200px) {
        flex-direction: column;
        justify-content: center;
     }
     @media (max-width:800px) {
         padding-top: 50px;
         padding-bottom: 50px;
     }
    &__item{
        flex-basis: 50%;
        h2{
            @media (max-width:800px) {
                padding-left: 0;
                margin: 0 auto;
                font-size:7vw;
                padding-top: 30px;
             }
        }
        img{
            width: 90vmin;
            height: 90vmin;
        }
        &__first,&__second{
            padding-left: 105px;
        }
        &__first{
            padding-top: 111px;
        }
        &__second{
            padding-bottom: 101px;
        }
        p{
            max-width: 555px;
            @media (max-width:800px) {
                padding-left: 0;
                margin: 0 auto;
                font-size:7vw;
             }
        }
        a{
          margin-left: 105px;
            text-decoration: none;
            color: #3cbef1;
            font-family: 'Fivo Sans Heavy Oblique';
            font-size: 18px;
        }
    }
}

.page-footer{
   background: url("../img/footerbg.png") no-repeat center;
   background-size: cover;
   
    &__prices{
       display: grid;
       grid-template-columns: repeat(auto-fit,minmax(270px,350px));
       grid-row-gap: 20px;
       grid-auto-rows: 100vh;
       justify-content: center;
       padding-bottom: 204px;
       &__item{
           border-radius: 20px;
           background: #fff;
           text-align: center;
          
           &__head{
               background: $acient;
               text-align: center;
               border-top-left-radius: 20px;
               border-top-right-radius: 20px;
               h2{
                   font-size: 30px;
                   color: #fff;
                   padding-top: 45px;
                   padding-bottom: 41px;
                   text-transform: uppercase;
               }
           }
          &__class{
               font-size: 30px;
               padding-top: 75px;
           }
        ul{
            padding: 0;
            padding-left: 100px;
            text-align: left;
            li{
                list-style-type: none;
                position: relative;
                font-family: 'Fivo Sans Regular';
                color: $gray;
                font-size: 18px;
                padding-top: 51px;
                span{
                    background: $acient;
                    height: 39px;
                    width: 39px;
                    display: inline-block;
                    border-radius: 50%;
                    position: absolute;
                    left: -48px;
                    top: 40px;
                    i{
                      color: #fff;
                       padding-left: 10px;
                        padding-top: 10px;
                        
                    }
                }
            }
        }
        h4{
            font-size: 131px;
            font-family: 'Fivo Sans Regular';
            color: $gray;
            position: relative;
            text-align: left;
            padding-left: 70px;
            margin-top: 90px;
            @media (max-width:400px) {
               font-size: 30vw;
            }
            span{
                font-size: 20px;
                @media (max-width:400px) {
                    font-size: 15px;
                 }
            }
        }
        h4::before{
            position: absolute;
            content: "$";
            font-size: 48px;
            left: 27px;
            top: 0;
        }
        p{
            font-size: 15px;
            text-align: center;
            padding-bottom: 65px;
        }

        a{
            margin-bottom: 56px;
            font-family: 'Fivo Sans Medium';
        }

        &__first{
            padding: 31px;
            background-image: linear-gradient(to right top, #5fcef5, #68d3f6, #71d9f8, #7adef9, #83e3fb);
            text-decoration: none;
            color: #fff;
            border-radius: 38px;
            
        }
        &__second{
            text-decoration: none;
            color: #fff;
            border-radius: 38px; 
            padding: 31px;
            background: $acient;
            display: inline-block;
        }
        &__third{
            text-decoration: none;
            color: #fff;
            border-radius: 38px; 
            padding: 31px;
            background-image: linear-gradient(to right top, #21acdd, #4bb8df, #69c3e2, #84cfe5, #9ddae9);
        }
       }
       &__item:nth-child(1){
        z-index: 0;
        box-shadow: -5px 6px 12px 0px #f09699;
        li{ 
            span{
                background-image: linear-gradient(to right bottom, #9de7f9, #8bddfb, #7ed3fd, #75c8fe, #74bcfe);
            }
        }
    ul{
        padding-top: 80px;
    }
    }
    &__item:nth-child(2){
        box-shadow: 0px 7px 15px 2px #f09699;
        z-index: 1;
    }
    &__item:nth-child(3){
        z-index: 0;
        box-shadow: 5px 6px 12px 0px #f09699;
        li{ 
        span{
            background-image: linear-gradient(to right bottom, #9de7f9, #8bddfb, #7ed3fd, #75c8fe, #74bcfe);
        }
    }
        h4{
            padding-left: 108px;
            margin-top: 130px;
        }

        h4::before{
            left: 64px;
        }
    }
   
   }
   &__bottom{
       background: #c1f2fe;
       border-top-left-radius: 176px;
       border-top-right-radius: 176px;
   &__content{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(177px,200px));
    grid-auto-rows: max-content;
    grid-column-gap: 173px;
    grid-row-gap: 30px;
    padding-top: 116px;
    padding-bottom: 108px;
    justify-content: center;
    &__item{
        &__address{
            padding-bottom: 47px;
        }
        h4{
            color: $black;
            font-family: 'Fivo Sans Heavy';
            font-size: 18px;
            text-transform: uppercase;
            padding-bottom: 60px;
        }
        ul{
            padding: 0;
            li{
                list-style-type:none;
                padding-top: 24px;
                a{
                    text-decoration: none;
                    color: $gray;
                    font-family: 'Fivo Sans Regular';
                    font-size: 16px;
                    
                }
            }
            li:first-child{
                padding-top: 0;
            }
        }
    }
  
    
   }
   &__copy{
       text-align: center;
padding-bottom: 60px;
   }
}
}


/* animation */

.page-header__menu__hamburger__active{
    span{
        height: 0;
    }
    span::after{
        transform: rotate(45deg);
        transition: 0.2s ease-in-out;
    }
    span::before{
        transform: rotate(-45deg);
        transition: 0.2s ease-in-out;
    }
}

.showmenu{
    display: block;
    transform: scale(1);
  
}



